@import "../../../assets/styles/app";

.planetSlider {
  position: relative;
  background: rgba(21, 24, 40, 0.8);
  backdrop-filter: blur(12px);
  border-radius: 15px;
  border: 1px solid rgba(255, 255, 255, 0.06);
  width: 100%;
  height: 448px;

  @include desktop {
    height: 100%;
  }

  &:after {
    display: block;
    content: "";
    position: absolute;
    top: 220px;
    left: 0;
    right: 0;
    height: 1px;
    background: #1F202F;

    @include desktop {
      top: 314px;
    }
  }

  .slide {
    box-sizing: border-box;
    padding: 240px 20px 25px;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    overflow: hidden;

    @include desktop {
      padding: 340px 25px 25px;
    }

    .title {
      font-weight: 600;
      font-size: 20px;
      line-height: 130%;
      letter-spacing: -0.02em;
      color: #FFFFFF;

      @include desktop {
        font-size: 24px;
      }
    }

    .text {
      margin-top: 15px;
      font-weight: 400;
      font-size: 13px;
      line-height: 150%;
      letter-spacing: -0.01em;
      color: #FFFFFF;
      opacity: 0.6;

      @include desktop {
        font-size: 14px;
      }
    }

    .joinBtn {
      margin-top: 40px;
      width: 137px;
    }

    .img {
      display: block;
      position: absolute;
      top: -65px;
      left: 50%;
      transform: translateX(-50%);
      width: auto;
      height: 380px;

      @include desktop {
        top: -65px;
        height: 480px;
      }
    }

  }


}
